.section {
  &__title {
    padding-top: 2rem;
    margin-top: 0;
    margin-bottom: 3rem;
    font-size: 2.5rem;
    text-align: center;
    font-family: $title-font;

    &--l {
      font-size: 3.5rem;
    }

    &--accent {
      color: $acccent;
    }
  }

  &__description {
    font-size: 1rem;
    opacity: .35;
    text-align: center;
  }

  &--bg-g {
    background-color: $gray;
  }
}

.feature {
  max-width: 24rem;

  &__title {
    margin-bottom: 1rem;
    font-size: 1.5rem;
    font-family: $title-font;
  }

  &__description {
    font-size: 1rem;
    opacity: .35;
  }

  &__text {
    font-size: 1rem;
    line-height: 1.5;
  }
}

.animated-group {
  &:not(:first-of-type) {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }

  &__img-wrapper {
    flex-grow: 1;
    text-align: center;
    opacity: 0;

    > img {
      display: inline-block;
    }
  }

  .gs-f1-t,
  .gs-f2-t {
    opacity: 0;
  }
}

.gs-tile_1,
.gs-tile_2 {
  li {
    opacity: 0;
  }
}

.tile {
  padding: 2rem 0;

  &__row {
    display: flex;
    list-style: none;
  }
}

.tile-item {
  position: relative;
  min-width: 9.56rem;
  margin: 0 .5rem;
  padding: 1rem;
  background-color: $white;
  border-radius: .4rem;
  font-size: .875rem;

  &--small {
    display: flex;
    min-width: 8.34rem;
    padding: 0;
    overflow: hidden;

    .tile-item__title {
      display: inline-block;
      padding: .5rem;
    }
  }

  &__icon {
    &--top {
      position: absolute;
      top: 0;
      right: 1rem;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
      transform: translateY(-50%);
    }

    &--left {
      display: inline-block;
      width: 3.125rem;
      height: 100%;
    }

    &--orange {
      background-color: $orange;
    }

    &--green {
      background-color: $green;
    }
  }

  &__title {
    margin: 0;
    opacity: .4;
    text-transform: uppercase;
  }

  &__value {
    font-weight: bold;
  }

  &__score {
    &--positive {
      color: $green;
    }

    &--negative {
      color: $red;
    }
  }
}
