*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-size: 16px;
  font-family: $base-font;
  line-height: 1.4;
}

body {
  color: $base-txt;
}

.clearfix::after {
  content: ".";
  display: block;
  clear: both;
  font-size: 0;
  height: 0;
  visibility: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  position: relative;
  max-width: 1110px;
  margin-left: auto;
  margin-right: auto;
}

.btn {
  padding: 1.1rem 2.3rem;
  color: $blue;
  background-color: $blue_012;
  border-radius: .375rem;
  border: none;

  &--white {
    background-color: $white;
  }
}

.hint {
  padding: 2rem 3rem;
  color: $blue;
  background-color: $blue_012;
  border-radius: .4rem;
}
